import Module from './module/Module'

export default class extends Module {

  initialize() {

    const methods = [
      'setupListeners',
      'handleClick'
    ]

    methods.forEach(method => {
      this[method] = this[method].bind(this)
    })

    this.setupListeners()
  }

  setupListeners() {

    this.el.addEventListener("click", event => this.handleClick(event));

  }

  handleClick(event) {
    console.log(event)
  }

  get foo() {
    return "bar"
  }

}